input.rtl,
input[dir=rtl] {
  direction: rtl;
}
.input[dir=rtl],
.input.rtl {
  flex-flow: row-reverse nowrap;
}
.input[dir=rtl] input,
.input.rtl input {
  order: 2;
  direction: rtl;
}
.input[dir=rtl] .button-group,
.input.rtl .button-group {
  order: 1;
}
.input[dir=rtl] .prepend,
.input.rtl .prepend {
  order: 3;
}
.file[dir=rtl] .prepend,
.file.rtl .prepend {
  order: 3;
}
.file[dir=rtl] .caption,
.file.rtl .caption {
  order: 2;
  text-align: right;
}
.file[dir=rtl] button,
.file.rtl button {
  order: 1;
  margin-left: 0;
  margin-right: auto;
}
.textarea[dir=rtl],
.textarea.rtl {
  flex-direction: row-reverse;
}
.textarea[dir=rtl] .prepend,
.textarea.rtl .prepend {
  order: 2;
}
.textarea[dir=rtl] textarea,
.textarea.rtl textarea {
  order: 1;
}
.textarea[dir=rtl] .input-clear-button,
.textarea.rtl .input-clear-button {
  right: auto;
  left: .25rem;
}
.textarea[dir=rtl] .input-clear-button ~ textarea,
.textarea.rtl .input-clear-button ~ textarea {
  padding-left: 48px;
  padding-right: .75rem;
}
.select[dir=rtl],
.select.rtl {
  flex-direction: row-reverse;
}
.select[dir=rtl] input,
.select.rtl input {
  order: 1;
}
.select[dir=rtl] .prepend,
.select.rtl .prepend {
  order: 2;
}
.select[dir=rtl].dropdown-toggle,
.select.rtl.dropdown-toggle {
  padding-right: 0!important;
  padding-left: 1.5rem;
}
.select[dir=rtl].dropdown-toggle::before,
.select.rtl.dropdown-toggle::before {
  margin-left: 1.25rem;
  left: 0;
}
